@import url(https://fonts.googleapis.com/css?family=Eczar&display=swap);
@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro&display=swap);
@import url(https://fonts.googleapis.com/css?family=Kanit&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.projects-each {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 20px; 
    box-shadow: 20px 15px 20px #F9F871;
}
.image-wrapper img {
    display: flex;
    width: 500px;
    height: auto;
    padding: 15px;
    margin-top: 20px;
}
.project-info {
    display: flex;
    flex-direction: column;
    padding-right: 20%;
    width: 400px;
    height: auto;
    padding: 15px;
    font-size: 20px;
    font-family: 'Kanit', sans-serif;
    color: #F9F871;
}
.projects>div {
    flex: 0 1 50%;
    box-sizing:border-box;
    display: flex;
    justify-content: space-evenly;
}
.project-title{
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    font-weight: bold;
    font-size: 30px;
    padding: 10px
}
.project-link a{
    color: #F9F871;
    text-decoration: none;
}
.project-title:hover{
    background-color: #00257E;
    color: white; 
}
.project-container {
    display: flex;
    width: 298px;
    flex-direction: column;   
}
.projects {
    display: flex;
    justify-content: center;
    margin-top: 10%;
    margin-bottom: 10%;
}
body {
    background-image: -webkit-gradient(linear, left top, right bottom, from(#031354), color-stop(#002566), color-stop(#003777), color-stop(#004a86), color-stop(#005c95), color-stop(#0070a6), color-stop(#0084b5), color-stop(#0099c2), color-stop(#00b4cf), color-stop(#00cece), color-stop(#00e6bf), to(#5ffba5));
    background-image: -webkit-linear-gradient(left top, #031354, #002566, #003777, #004a86, #005c95, #0070a6, #0084b5, #0099c2, #00b4cf, #00cece, #00e6bf, #5ffba5);
    background-image: linear-gradient(to right bottom, #031354, #002566, #003777, #004a86, #005c95, #0070a6, #0084b5, #0099c2, #00b4cf, #00cece, #00e6bf, #5ffba5);
}

.header {
    background-color: black;
    height: 300px;

}
.title{
  position: relative;
  top: 40%;  
  width: 24em;
  color: white;
  margin: 0 auto;
  border-right: 2px solid rgba(255,255,255,.75);
  font-size: 180%;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);    
}
.typewriter{
  -webkit-animation: typewriter 2s steps(44) .2s 1 normal both,
             blinkTextCursor 500ms steps(44) infinite normal;
          animation: typewriter 2s steps(44) .2s 1 normal both,
             blinkTextCursor 500ms steps(44) infinite normal;
}
@-webkit-keyframes typewriter{
  from{width: 0;}
  to{width: 24em;}
}
@keyframes typewriter{
  from{width: 0;}
  to{width: 24em;}
}
@-webkit-keyframes blinkTextCursor{
  from{border-right-color: rgba(255,255,255,.75);}
  to{border-right-color: transparent;}
}
@keyframes blinkTextCursor{
  from{border-right-color: rgba(255,255,255,.75);}
  to{border-right-color: transparent;}
}
.footer {
    display: flex;
    background-color: black;
    height: 500px;
}
.contact-form{
    display: flex;
    flex-direction: column;
    padding: 50px;
    width: 100%;
    margin: 50px;
    margin-top: 20px
}

.contact-form input{
    font-size: 20px;
    margin-top: 10px;
    width: auto;
    height: 30px;
}

.contact-form textarea{
    font-size: 20px;
    margin-top: 20px;
    width: auto;
    height: 150px;
}

button {
    background-color: red;
    margin-top: 20px;
    font-size: 20px;
    width: 30%;
    height: auto;
}
.zoom{
    display: flex;
    justify-content: center;
    -webkit-transition: -webkit-transform .2s;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}
.zoom:hover { /* IE 9 */
    -webkit-transform: scale(1.2); /* Safari 3-8 */
    transform: scale(1.2); 
}
.self-portrait {
    margin: 30px;
    border:white 10px solid;
    border-radius: 50%;
}
img { 
    width: auto;
    height: 300px; 
} 

.contact-links img{
    display: flex;
    justify-content: space-evenly;
    margin-top: 8%;
    margin-left: 20%;
    height: 20%;
    width: auto;
}
.about-me {
    display: flex;
    flex-direction: column;
    background-color: #F9F871;
    border-radius: 20%;
    margin: 8%;
    padding: 5%;
}
.about-me h3 {
    overflow: hidden;
    text-align: center;
    font-family: 'Source Code Pro', monospace;
    font-size: 40px;
    margin-bottom: 5px;
   }
h3:before,
h3:after {
    background-color: black;
    content: "";
    display: inline-block;
    height: 5px;
    position: relative;
    vertical-align: middle;
    width: 20%;
}
h3:before {
    right: 0.5em;
    margin-left: -50%;
}
h3:after {
    left: 0.5em;
    margin-right: -50%;
}
.about-me p{
    padding: 20px;
    font-family: 'Kanit', sans-serif;
    font-size: 30px;
}
.about-me h2 {
    overflow: hidden;
    text-align: center;
    font-family: 'Source Code Pro', monospace;
    font-size: 40px;
    margin-bottom: 50px;
   }
h2:before,
h2:after {
    background-color: black;
    content: "";
    display: inline-block;
    height: 5px;
    position: relative;
    vertical-align: middle;
    width: 20%;
}
h2:before {
    right: 0.5em;
    margin-left: -50%;
}
h2:after {
    left: 0.5em;
    margin-right: -50%;
}

.about-me-images {
    font-family: 'Eczar', serif;
    font-size: 30px;
    margin: 30px;
    text-align: center;
}

.about-me-images img{
    height: 100px;
    width: auto;
    
}
